@define-mixin gradient-background $color-1: rgba(109,179,242,1), $color-2: rgba(84,163,238,1), $color-3: rgba(54,144,240,1), $color-4: rgba(30,105,222,1) {
  background: linear-gradient(135deg, $color-1 0%, $color-2 50%, $color-3 51%, $color-4 100%);
}



